.formContainer {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  max-height: 100%;
  height: 100%;
}
.formContainer * {
  box-sizing: border-box;
}

.form {
  padding-top: 24px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 20px;
  height: calc(100% - 90px);
}

.formHeader {
  display: flex;
  justify-content: space-between;
  color: #000;
  padding: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.formHeaderTitle {
  font-size: 30px;
  font-weight: 500;
  line-height: 40px;
}

.closeBtn {
  all: unset;
  cursor: pointer;
  border: 1px solid #8a9393;
  border-radius: 10px;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sectionNavigation {
  border-bottom: 1px solid #8a9393;
}

.sectionNavigationBtn {
  all: unset;
  cursor: pointer;
  padding: 20px 10px;
  transition: opacity 200ms ease;
}

.sectionNavigationBtnText {
  transition: opacity 100ms ease;
}

.sectionNavigationBtn:not(.active) .sectionNavigationBtnText:hover {
  opacity: 0.7;
}

.sectionNavigationBtn.active {
  color: #212121;
  border-bottom: 2px solid #212121;
}

.buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  padding: 20px;
  border-top: 1px solid #8a9393;
  margin-top: auto;
}

.editButtons {
  display: flex;
  gap: 5px;
}

.editButtons button {
  all: unset;
  cursor: pointer;
  border: 1px solid #8a9393;
  border-radius: 10px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.editButtons {
  display: flex;
  gap: 5px;
}

.editButtons button {
  all: unset;
  cursor: pointer;
  border: 1px solid #8a9393;
  border-radius: 10px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input {
  padding: 0px 20px;
  border-radius: 15px;
  height: 50px;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  color: #212121;
  width: 100%;
  max-width: 100%;
}

.invalidFormNotificationContainer {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.invalidFormNotificationTitle {
  font-size: 14px;
  font-weight: bold;
}

.fieldWrapper:empty {
  display: none;
}