.priority {
  border-radius: 8px;
  text-align: center;
  line-height: 34px;
  display: inline-block;
  padding: 0 25px 0 25px;
}

.priority.high {
  background-color: rgba(254, 6, 6, 0.2);
  color: #fe0606;
}

.priority.medium {
  background-color: rgba(255, 126, 7, 0.2);
  color: #ff7e07;
}

.priority.low {
  background-color: rgba(14, 181, 0, 0.2);
  color: #0eb500;
}