.tooltipIcon {
  width: 12px;
  height: 12px;
  cursor: pointer;
}

.labelWrapper {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-bottom: 5px;
  width: 100%;
}

.tooltip {
  position: absolute;
  background: #212121;
  padding: 5px;
  border-radius: 10px 10px 10px 10px;
  font-size: 12px;
  min-width: 110px;
  text-align: center;
  color: #fff;
  width: 137px;
  z-index: 9999999999;
  transform: translateY(-100%);
  pointer-events: none;
}
.tooltip::after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  top: 100%;
  left: 10px;
  border-top: 10px solid #212121;
  border-right: 5px solid transparent;
  border-bottom: 0;
  border-left: 5px solid transparent;
}