.dateInput {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  height: 42px;
  margin: 0;
  padding: 0 40px 0 16px;
  color: #1f2937;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: 300;
  font-style: normal;
  width: 100%;
  z-index: 1;
  position: relative;
  background-image: url("../../../../../../public/images/icon-calendar-filled.png");
  background-repeat: no-repeat;
  background-position: right 12px center;
}
.dateInput:active, .dateInput:focus, .dateInput:focus-within, .dateInput:focus-visible {
  z-index: 9999999 !important;
}

.inputWrapper :global(.react-datepicker-wrapper) {
  width: 100%;
}
.inputWrapper :global(.react-datepicker-popper) {
  z-index: 99999;
}
.inputWrapper :global(.react-datepicker__close-icon) {
  z-index: 99999;
}
.inputWrapper :global(.react-datepicker__close-icon)::after {
  background-color: #1f6763;
}

.calendar {
  z-index: 9;
}

.day:global(.react-datepicker__day--in-range) {
  background-color: #1f6763;
}
.day:global(.react-datepicker__day--in-range):hover {
  background-color: #1f6763;
}
.day:global(.react-datepicker__day--in-selecting-range) {
  background-color: rgba(31, 103, 99, 0.3137254902);
}
.day:global(.react-datepicker__day--selecting-range-end) {
  background-color: #1f6763;
}
.day:global(.react-datepicker__day--selecting-range-end):hover {
  background-color: #1f6763;
}
.day:global(.react-datepicker__day--range-start) {
  background-color: rgba(31, 103, 99, 0.3137254902);
}

.error {
  color: red;
  margin-top: 5px;
}