.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  border: 1px solid transparent;
  border-radius: 8px;
  padding: 10px 28px 10px 28px;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: normal;
  font-style: normal;
  min-width: 110px;
  transition: 0.2s all ease;
  cursor: pointer;
  text-decoration: none;
  position: relative;
  white-space: nowrap;
  /* HTML: <div class="loader"></div> */
}
.button.primary {
  background: #1f6763;
  color: #fff;
  border: none;
}
.button.primary:hover {
  background: rgb(19.2014925373, 63.7985074627, 61.3208955224);
}
.button.secondary {
  background-color: #fff;
  color: #333;
  border-color: #8a9393;
}
.button.secondary:hover {
  opacity: 0.7;
}
.button.secondary-green {
  background-color: #fff;
  color: #1f6763;
  border: 1px solid #1f6763;
}
.button.secondary-green:hover {
  color: #fff;
  background-color: #1f6763;
}
.button.no-border {
  border: none;
  background: transparent;
}
.button.disabled {
  opacity: 0.5;
}
.button .loader {
  width: 20px;
  padding: 2px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: #25b09b;
  --_m:
      conic-gradient(#0000 10%, #000),
      linear-gradient(#000 0 0) content-box;
  -webkit-mask: var(--_m);
  mask: var(--_m);
  -webkit-mask-composite: source-out;
  mask-composite: subtract;
  animation: l3 0.5s infinite linear;
}
@keyframes l3 {
  to {
    transform: rotate(1turn);
  }
}