.field {
  display: flex;
  flex-direction: column;
  gap: 3px;
  position: relative;
}

.inputContainer {
  display: flex;
}

.field.invalid .uploadArea {
  border-color: red;
}

.errorContainer {
  color: red;
  position: absolute;
  bottom: -18px;
  left: 0;
}

.uploadArea {
  border: 2px dashed #8a9393;
  border-radius: 15px;
  min-height: 120px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  background-color: #fafafa;
}
.uploadArea:hover {
  border-color: #0073aa;
  background-color: #f0f8ff;
}
.uploadArea.dragActive {
  border-color: #0073aa;
  background-color: #e6f3ff;
  transform: scale(1.02);
}

.fileInput {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 1;
}

.uploadContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
  pointer-events: none;
}

.uploadIcon {
  color: #8a9393;
  margin-bottom: 12px;
  transition: color 0.3s ease;
}
.uploadArea:hover .uploadIcon {
  color: #0073aa;
}

.uploadText {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.uploadTitle {
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  font-weight: 500;
  color: #212121;
}

.uploadSubtitle {
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  color: #666;
}

.filePreview {
  border: 1px solid #8a9393;
  border-radius: 15px;
  padding: 16px 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f9f9f9;
  min-height: 60px;
}

.fileInfo {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.fileIcon {
  color: #8a9393;
  flex-shrink: 0;
}

.fileName {
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  color: #212121;
  word-break: break-all;
  line-height: 1.4;
}

.removeButton {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 8px;
  color: #666;
  transition: all 0.3s ease;
  flex-shrink: 0;
}
.removeButton:hover {
  background-color: #ffebee;
  color: #d32f2f;
}
.removeButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}