.input {
  padding: 0 12px 0 12px;
  padding-right: 40px;
  background: url("../../../../../../public/images/search-icon.png") no-repeat calc(100% - 10px) center;
  background-size: 16px 16px;
  background-color: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  color: #1f2937;
  height: 42px;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: normal;
  font-style: normal;
}

@media (max-width: 768px) {
  .input,
  .inputContainer {
    width: 100%;
    order: 3;
  }
}