.eventWrapper {
  border-radius: 4px;
}
.eventWrapper.pending :global(.rbc-event) {
  border-left-color: #ff7e07;
}
.eventWrapper.approved :global(.rbc-event) {
  border-left-color: #0eb500;
}
.eventWrapper.paid :global(.rbc-event) {
  border-left-color: #ff7e07;
}
.eventWrapper.paid_approved :global(.rbc-event) {
  border-left-color: #0eb500;
}
.eventWrapper.woocommerce :global(.rbc-event) {
  border-left-color: #ff7e07;
}
.eventWrapper.added_by_admin_not_paid :global(.rbc-event) {
  border-left-color: #ff7e07;
}
.eventWrapper.added_by_admin_paid :global(.rbc-event) {
  border-left-color: #0eb500;
}
.eventWrapper.arrived :global(.rbc-event) {
  border-left-color: #0eb500;
}
.eventWrapper.cancelled :global(.rbc-event) {
  border-left-color: #fe0606;
}
.eventWrapper.rejected :global(.rbc-event) {
  border-left-color: #fe0606;
}
.eventWrapper.noshow :global(.rbc-event) {
  border-left-color: #fe0606;
}

.wrapper {
  padding: 0 20px 0 20px;
}
.wrapper :global(.rbc-header) {
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.wrapper :global(.rbc-toolbar) {
  margin: 20px;
}
.wrapper :global(.rbc-toolbar) label {
  padding: 20px 0;
}
.wrapper :global(.rbc-toolbar) button {
  transition: 0.2s all ease-in-out;
}
.wrapper :global(.rbc-toolbar) button.rbc-active, .wrapper :global(.rbc-toolbar) button:hover, .wrapper :global(.rbc-toolbar) button:global(.rbc-active), .wrapper :global(.rbc-toolbar) button:active, .wrapper :global(.rbc-toolbar) button:focus {
  background-color: #1f6763;
  color: #ffffff;
  border-color: #1f6763;
}
.wrapper :global(.rbc-toolbar) button:hover {
  background-color: rgb(36.8992537313, 122.6007462687, 117.8395522388);
}
.wrapper :global(.rbc-event) {
  border-color: #d8eded;
  margin: 0;
  border-left: 10px solid transparent;
  border-radius: 4px;
  padding: 0 0 0 2px;
  background-color: transparent;
  color: #212121;
}
.wrapper .toolWrapper {
  margin-top: 20px;
  display: flex;
  gap: 10px;
  align-items: flex-end;
  margin-bottom: 20px;
}
.wrapper .toolWrapper .filterWrapper {
  margin-bottom: 0;
}
@media (max-width: 768px) {
  .wrapper .toolWrapper {
    flex-flow: column-reverse;
  }
  .wrapper .toolWrapper .addButton {
    margin-left: 0;
  }
}
.wrapper :global(.rbc-show-more) {
  color: #1f6763;
}
.wrapper .addButton {
  margin-left: auto;
}
.wrapper :global(.rbc-event[role="button"]) {
  background-color: inherit;
}

:global(.rbc-overlay) :global(.rbc-event) {
  border-color: #d8eded;
  margin: 0;
  border-left: 10px solid transparent;
  border-radius: 4px;
  padding: 0 0 0 2px;
  background-color: transparent;
  color: #212121;
}
:global(.rbc-overlay) :global(.rbc-event):not(:last-child) {
  margin-bottom: 1px;
}