.table {
  border-collapse: collapse;
}
.table :global(td) {
  text-align: left;
  padding: 15px;
  vertical-align: top;
  background-color: #f9fafb;
}
@media (max-width: 768px) {
  .table {
    width: 100%;
  }
  .table tr {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(1, 1fr);
    margin-top: 12px;
    padding-top: 12px;
    border-top: 1px solid #e5e7eb;
    width: 100%;
  }
  .table td {
    background-color: transparent;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    gap: 12px;
  }
  .table td:nth-child(1) {
    display: none;
  }
}