.field {
  display: flex;
  flex-direction: column;
  gap: 3px;
  position: relative;
}

.inputContainer {
  display: flex;
}

.field.invalid .input {
  border-color: red;
}

.labelContainer {
  display: flex;
}

.errorContainer {
  color: red;
  position: absolute;
  bottom: -18px;
  left: 0;
}

.input {
  border: 1px solid #8a9393;
  border-radius: 15px;
  height: 50px;
  margin: 0;
  padding: 0 20px 0 20px;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  color: #212121;
  width: 100%;
}

.rawEditor {
  width: 100%;
  min-height: 280px;
  border-radius: 2px;
  padding: 10px;
}

.editorToggle {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.editorToggle .editorToggleButton {
  padding: 5px 8px;
  background-color: darker(#f6f7f7, 5);
  color: #595959;
  cursor: pointer;
  border: 1px solid #dcdcde;
}
.editorToggle .editorToggleButton.editorToggleButtonActive {
  background-color: #f6f7f7;
}