.wrapper input {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  height: 50px;
  width: 50px;
  -webkit-appearance: none;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  cursor: pointer;
}
.wrapper input::-webkit-color-swatch-wrapper {
  padding: 0;
}
.wrapper input::-webkit-color-swatch {
  border: none;
}
.wrapper .boxWrapper {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: flex-start;
  border: 1px solid #8a9393;
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
}
.wrapper .boxWrapper span {
  cursor: text;
}