.wrapper {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 850px) {
  .wrapper {
    display: grid;
    gap: 16px;
    grid-template-columns: repeat(2, 1fr);
  }
}

.card {
  background-color: #fff;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  padding: 16px;
}

.toolPanel {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
}
.toolPanel.expandable {
  justify-content: space-between;
}

.firstStage {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 12px;
}

.mappedItems {
  display: grid;
  gap: 12px;
  grid-template-columns: repeat(2, 1fr);
}

.rowItem .rowItemTitle {
  color: #6b7280;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 0%;
  margin-bottom: 3px;
}
.rowItem .rowItemValue {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 0%;
  color: #1f2937;
}

.expandButton {
  color: #0f766e;
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
}
