.fields {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.fields.collapsed {
  display: none;
}

.fieldsContainer {
  display: flex;
  flex-direction: column;
  gap: 24px;
  background-color: #efefef;
  padding: 6px 4px;
  border-radius: 8px;
}
.fieldsContainer:not(.containerCollapsed) {
  padding: 12px;
}

.fieldSummary {
  display: flex;
  align-items: center;
  gap: 10px;
}

.slugInput {
  width: 70%;
}

.widthField {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.requiredField {
  flex-direction: column-reverse;
}

.menuBtn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 1px 6px;
  min-width: unset;
}

.moreIcon {
  width: 24px;
  height: 24px;
  min-width: 24px;
  min-height: 24px;
  max-width: 24px;
  min-height: 24px;
}

.deleteIcon {
  width: 16px;
  height: 16px;
  min-width: 16px;
  min-height: 16px;
  max-width: 16px;
  min-height: 16px;
}

.menuBtn:hover {
  opacity: 0.7;
}

.menuBtn.hidden {
  visibility: hidden;
}

.menuBtn.open {
  transform: rotate(180deg);
}

.handle {
  border: none;
  background-color: transparent;
  cursor: grab;
  padding: 0;
  min-width: unset;
  display: flex;
  align-items: center;
  justify-content: center;
}
.handle svg {
  width: 24px;
  height: 24px;
}

.requiredToggleWrapper {
  position: relative;
  display: inline-block;
}

.requiredToggleHoverArea {
  display: inline-block;
}

.requiredTooltip {
  display: none;
  position: absolute;
  right: 0;
  left: auto;
  top: -32px;
  transform: none;
  background: #222;
  color: #fff;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 13px;
  white-space: nowrap;
  z-index: 10;
  pointer-events: none;
  opacity: 0.95;
  max-width: 220px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.requiredToggleWrapper:hover .requiredTooltip {
  display: block;
}