.wrapper tr {
  background-color: #f9fafb !important;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table :global(td) {
  text-align: left;
  padding: 15px;
  vertical-align: top;
}
@media (max-width: 768px) {
  .table :global(td) {
    padding: 0;
  }
}
@media (max-width: 768px) {
  .table :global(tr) {
    display: grid;
    gap: 16px;
    margin-top: 16px;
    padding-top: 16px;
    border-top: 1px solid #e5e7eb;
    background-color: transparent !important;
  }
}

.description {
  text-align: left;
  padding: 15px;
}

.column {
  display: flex;
  flex-flow: column;
  gap: 16px;
}

.columnItem {
  display: flex;
  flex-flow: column;
  gap: 6px;
}
.columnItem > span {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 0%;
  color: #6b7280;
}
.columnItem p {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0%;
  margin: 0;
  color: #111827;
}
@media (max-width: 768px) {
  .columnItem.wrapMobile {
    flex-flow: row;
    justify-content: space-between;
    align-items: center;
  }
}

@media (max-width: 768px) {
  .customDetailItems {
    display: flex;
    flex-flow: column;
    gap: 6px;
    padding: 12px;
    background-color: #f9fafb;
    border-radius: 8px;
  }
  .customDetailItems p {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.emailSelect {
  padding: 8px 12px;
  padding-left: 40px;
  background-image: url("../../../../../../public/images/icon-send.svg");
  background-position: 12px center;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  color: #1f6763;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: normal;
  font-style: normal;
  -webkit-appearance: listbox;
  appearance: listbox;
}

.inputNote {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  height: 140px;
  padding: 8px;
}

.emailSelectWrapper {
  display: flex;
  flex-flow: column;
  gap: 6px;
}

.customDetailWrapper {
  max-width: 250px;
}
@media (max-width: 768px) {
  .customDetailWrapper {
    max-width: 100%;
  }
}

.mobileDivider {
  display: none;
}
@media (max-width: 768px) {
  .mobileDivider {
    display: flex;
    width: 100%;
    margin-bottom: 16px;
    border-top: 1px solid #e5e7eb;
  }
}