.wrapper {
  position: relative;
}
.wrapper:active, .wrapper:focus, .wrapper:focus-visible, .wrapper:focus-within {
  z-index: 3 !important;
}

.proLabel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
  display: flex;
  align-items: flex-end;
  justify-content: center;
  z-index: 1;
}

.upgradeLink {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.1411764706);
  padding: 5px;
  margin-bottom: 10px;
  text-decoration: none;
  color: #1f6763;
  transition: 0.3s all ease-in;
  box-shadow: 0 0 3px rgba(51, 51, 51, 0.1568627451);
  border: 1px solid rgba(51, 51, 51, 0.1568627451);
}
.upgradeLink .locked,
.upgradeLink .unlock {
  width: auto;
  height: 16px;
  object-fit: contain;
}
.upgradeLink .unlock {
  display: none;
}
.upgradeLink:hover {
  padding: 8px 12px;
}
.upgradeLink:hover .unlock {
  display: flex;
}
.upgradeLink:hover .locked {
  display: none;
}