.wrapper {
  box-shadow: 0 0 15px rgba(161, 164, 182, 0.5);
  border-radius: 20px;
  padding: 20px 30px 30px 30px;
  background-color: #fff;
}
.wrapper .topContents {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.wrapper .topContents .title {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  font-size: 46px;
  line-height: 1.5;
  color: #212121;
  margin: 0;
}
.wrapper .topContents .iconWrapper {
  display: flex;
  width: 48px;
  height: 48px;
  min-width: 48px;
  min-height: 48px;
  background: #cbe7e7;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  z-index: 1;
}
.wrapper .bottomContents {
  font-weight: 300;
}