.selectField {
  display: flex;
  flex-direction: column;
  position: relative;
  gap: 3px;
  z-index: 1;
}
.selectField:active, .selectField:focus, .selectField:focus-within, .selectField:focus-visible {
  z-index: 9999999 !important;
}

.selectField.invalid {
  color: red;
}

.selectField.invalid .selectInput {
  border-color: red;
}

.errorContainer {
  display: absolute;
  color: red;
  bottom: -18px;
}

.selectInput {
  border-radius: 8px;
  border: 1px solid #e5e7eb;
  height: 42px;
  width: 100%;
  max-width: 100%;
  color: #1f2937;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: 300;
  font-style: normal;
}
.selectInput :global(.css-hlgwow) {
  padding-left: 12px;
}
.selectInput.preventOverlap {
  height: auto;
}
.selectInput :global(.css-1u9des2-indicatorSeparator) {
  display: none;
}
.selectInput :global(.css-tj5bde-Svg) {
  fill: #9ca3af;
}
.selectInput :global(.css-1jqq78o-placeholder) {
  color: #333333;
}

.multiValueMore {
  display: flex;
  min-width: 0;
  background-color: hsl(0, 0%, 90%);
  border-radius: 2px;
  margin: 2px;
  box-sizing: border-box;
  max-width: 100px;
  white-space: nowrap;
  border-radius: 2px;
  color: hsl(0, 0%, 20%);
  font-size: 85%;
  padding: 3px;
  padding-left: 6px;
  min-width: 30px;
}