.wrapper {
  position: relative;
  display: inline-block;
  width: auto;
  padding: 1px 6px;
  z-index: 0;
}
.wrapper.active {
  z-index: 9999;
}
.wrapper.active[data-title]::after {
  display: none;
}

.confirmation {
  position: absolute;
  width: max-content;
  line-height: 32px;
  background: #1f6763;
  border: 1px solid transparent;
  border-radius: 15px;
  color: #fff;
  padding: 0 20px 0 20px;
  cursor: pointer;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.2s all ease;
  z-index: -1;
}
.confirmation.opened {
  z-index: inherit;
}
.confirmation.opened.top {
  top: unset;
  bottom: calc(100% + 5px);
  left: 50%;
  transform: translateX(-50%);
}
.confirmation.opened.right {
  left: calc(100% + 5px);
  transform: translateY(-50%);
}
.confirmation.opened.bottom {
  top: calc(100% + 5px);
  transform: translate(-50%, 0) scale(1);
}
.confirmation.opened.left {
  left: calc(50% - 15px);
  transform: translate(-100%, -50%) scale(1);
}