.app {
  margin-bottom: 20px;
  padding-right: 20px;
}

.bookingsTable tr {
  display: grid;
  grid-template-areas: "select menu menu" "customer service date" "payment phone phone" "status status status";
}

.bookingsTable td[data-column-name=id] {
  display: none;
}

.bookingsTable td[data-column-name=select] {
  display: flex;
  justify-content: flex-start;
}

.bookingsTable td[data-column-name=menu] {
  display: flex;
  justify-content: flex-end;
}

.bookingsTable td[data-column-name=status] select {
  width: 100%;
}

@media screen and (min-width: 1200px) {
  .bookingsTable tr {
    display: table-row;
  }
  .bookingsTable td {
    display: table-cell !important;
  }
}
.exportButton {
  color: #a1a3b6;
  padding: 0;
  gap: 10px;
  transition: 0.3s all linear;
}
.exportButton img {
  transition: 0.3s all linear;
}
.exportButton:hover img {
  transform: translateX(3px);
}

.buttonNavigation {
  display: flex;
  gap: 10px;
  cursor: pointer;
  margin-left: 20px;
  margin-bottom: 20px;
  transition: 0.3s all ease;
}
.buttonNavigation.red {
  color: rgb(255, 70, 70);
}
.buttonNavigation.green {
  color: #1f6763;
}
.buttonNavigation.left:hover {
  transform: translateX(-5px);
}
.buttonNavigation.right:hover {
  transform: translateX(5px);
}