.formContainer {
  font-family: "Ubuntu", arial;
  display: flex;
  flex-direction: column;
  max-height: 100%;
  height: 100%;
}

.form {
  max-height: calc(100% - 75px);
  display: flex;
  flex-direction: column;
  gap: 24px;
  height: 100%;
}

.formHeader {
  display: flex;
  justify-content: space-between;
  color: #000;
  padding: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.formBody {
  padding: 20px;
  display: flex;
  flex-flow: column;
  overflow-y: scroll;
}

.formFooter {
  padding: 20px;
  margin-top: auto;
  display: flex;
  flex-flow: column;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.formHeaderTitle {
  font-size: 32px;
  font-weight: 600;
  line-height: 1;
}

.closeBtn {
  all: unset;
  cursor: pointer;
  border: 1px solid #8a9393;
  border-radius: 10px;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 10px;
}

.btnSave {
  margin-top: auto;
}