.sidebarContainer {
  display: none;
}
.sidebarContainer * {
  box-sizing: border-box;
}

.sidebarContainer.shown {
  --header-height: 32px;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 100000;
}

.sidebar {
  --sidebar-width: 540px;
  position: absolute;
  top: 0;
  right: calc(var(--sidebar-width) * -1);
  height: 100%;
  width: var(--sidebar-width);
  transition: right 150ms ease;
  background-color: white;
  display: flex;
  flex-direction: column;
}
@media (max-width: 768px) {
  .sidebar {
    width: 100%;
  }
}

.sidebar.shown {
  right: 0;
  z-index: 2;
}

.scrollLock {
  overflow: hidden;
}